/* Common Styles for all Themes */
body {
    font-family: 'Jost', sans-serif;
    transition: background-color 0.5s, color 0.5s;
    margin: 0;
    padding: 0;
    perspective: 1000px;
  }
  
  h1, p {
    margin: 10px 0;
  }
  
  /* Themes with Depth and Jost Font */
  body.theme1, body.theme2, body.theme3, body.theme4, body.theme5, body.theme6, body.theme7, body.theme8, body.theme9, body.theme10 {
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.4);
    color: #fff;
  }
  
  /* Theme Styles */
  body.theme1 { background: #222; }
  body.theme2 { background: #333; }
  body.theme3 { background: #191970; }
  body.theme4 { background: #708090; }
  body.theme5 { background: #004d00; }
  body.theme6 { background: #f8f8f8; color: #333; }
  body.theme7 { background: #faf0e6; color: #333; }
  body.theme8 { background: #afeeee; color: #333; }
  body.theme9 { background: #ffe4e1; color: #333; }
  body.theme10 { background: #fffff0; color: #333; }
  
  /* Update the App Styles */
  .App {
    text-align: center;
    margin-top: 50px;
    position: relative; /* Make the position relative */
  }
  
  /* Update the navbar styles */
  .navbar {
    background: rgba(0, 0, 0, 0.8);
    padding: 10px;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
    display: flex;
    justify-content: space-between; /* Space elements evenly */
    align-items: center; /* Align items to the center vertically */
    margin-bottom: 80px;
  }

  @media (max-width: 768px) {
    .navbar {
      flex-direction: column;
      align-items: center;
    }
  
    .navbar a {
      margin: 10px 0;
      text-align: center; /* Center the text within each link */
      width: 100%; /* Make links full width on mobile */
    }
  }
  
  
  /* Update the button styles */
  button.theme-button {
    padding: 10px; /* Adjust size based on content */
    font-size: 1em;
    cursor: pointer;
    border: 2px solid #fff;
    border-radius: 8px;
    background: linear-gradient(45deg, #4CAF50, #2196F3);
    color: #fff;
    transition: all 0.3s ease-in-out;
  }
  
  button.theme-button:hover {
    transform: scale(1.1);
    background: linear-gradient(45deg, #2196F3, #4CAF50);
    border-color: #2196F3;
  }
  
/* Landing Page Styles */
.landing-page {
    padding: 80px 3%; /* Adjust padding based on percentage */
    text-align: center;
    transform-style: preserve-3d;
    margin-top: 80px; /* Add margin to create space between navbar and landing page */
  }
  
  
  header {
    margin-bottom: 40px;
  }
  
  header h1 {
    font-size: 3em;
    color: #2196F3;
    transform: translateZ(50px);
  }
  
  header p {
    font-size: 1.2em;
    color: #777;
    transform: translateZ(30px);
  }
  
  /* Additional Styles for Enhanced Features Section */
  .features {
    position: relative;
    overflow: hidden;
  }
  
  .feature {
    position: relative;
    z-index: 1;
    transition: transform 0.3s ease-in-out;
  }
  
  .feature:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    background: linear-gradient(30deg, #ffdb01 0%, #ff851f 30%, #ff3d3d 52%, #ff0f6f 70%, #f0f 100%);
    transform-origin: bottom left;
    transform: scaleX(0);
    transition: transform 0.3s ease-in-out;
  }
  
  .feature:hover {
    transform: scale(1.05) rotateX(5deg) rotateY(5deg);
  }
  
  .feature:hover:before {
    transform-origin: top right;
    transform: scaleX(1);
  }
  
  /* Optional: Add animations to text */
  .feature h2, .feature p {
    transition: transform 0.3s ease-in-out;
  }
  
  .feature:hover h2, .feature:hover p {
    transform: translateY(-10px);
  }
  
  @media (max-width: 768px) {
    .navbar {
      flex-direction: column; /* Stack elements vertically on smaller screens */
      align-items: center; /* Center items vertically */
    }
  
    .navbar a {
      margin: 10px 0; /* Adjust margin for links on mobile */
    }
  }
  
  footer {
    background: #222;
    color: #fff;
    padding: 20px 3%; /* Adjust padding based on percentage */
    transform: translateZ(10px);
  }
  
  footer p {
    margin: 0;
  }
  
  .logo {
    width: 50px; /* Set the desired width */
    height: auto; /* Maintain aspect ratio */
    margin-right: 10px; /* Adjust spacing as needed */
  }
  